import { useState, useEffect } from 'react'
import Navbar from '@/components/Navbar'
import ProductCard, { ProductGrid, CategoryHeader } from '@/components/ProductCard'
import MiniCart from '@/components/MiniCart'
import Footer from '@/components/Footer'
import { supabase } from '@/lib/supabase'

export default function Home() {
  const [products, setProducts] = useState([])
  const [extras, setExtras] = useState([])
  const [categories, setCategories] = useState([])
  const [selectedCategory, setSelectedCategory] = useState('all')
  const [session, setSession] = useState(null)
  const [loading, setLoading] = useState(true)
  
  // CART STATE
  const [cart, setCart] = useState([])
  const [isCartOpen, setIsCartOpen] = useState(false)

  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      setSession(data.session)
    })
    loadProducts()
    loadExtras()
    loadCart()
  }, [])

  // Cart aus localStorage laden
  const loadCart = () => {
    const savedCart = localStorage.getItem('simonetti-cart')
    if (savedCart) {
      setCart(JSON.parse(savedCart))
    }
  }

  // Cart speichern
  const saveCart = (newCart) => {
    setCart(newCart)
    localStorage.setItem('simonetti-cart', JSON.stringify(newCart))
  }

  const loadProducts = async () => {
    setLoading(true)
    
    const { data } = await supabase
      .from('products')
      .select('*')
      .order('category', { ascending: true })

    if (data) {
      setProducts(data)
      // Extract unique categories
      const uniqueCategories = [...new Set(data.map(p => p.category))]
      setCategories(uniqueCategories)
    }
    setLoading(false)
  }

  const loadExtras = async () => {
    const { data } = await supabase
      .from('extras')
      .select('*')
      .eq('active', true)
    
    if (data) setExtras(data)
  }

  // ADD TO CART mit Portionen & Extras
  const handleAddToCart = (product, portions = 1, selectedFlavors = [], selectedExtras = []) => {
    const cartItem = {
      ...product,
      quantity: portions,
      selectedFlavors: selectedFlavors,
      selectedExtras: selectedExtras,
      totalPrice: (product.price * portions) + selectedExtras.reduce((sum, e) => sum + e.price, 0),
      cartId: `${product.id}-${Date.now()}` // Unique ID f√ºr mehrfache gleiche Produkte
    }

    const newCart = [...cart, cartItem]
    saveCart(newCart)
    setIsCartOpen(true) // Auto-open cart
  }

  const updateCartQuantity = (cartId, newQuantity) => {
    if (newQuantity === 0) {
      removeFromCart(cartId)
      return
    }
    const newCart = cart.map(item => 
      item.cartId === cartId 
        ? { ...item, quantity: newQuantity, totalPrice: (item.price * newQuantity) + item.selectedExtras.reduce((sum, e) => sum + e.price, 0) }
        : item
    )
    saveCart(newCart)
  }

  const removeFromCart = (cartId) => {
    const newCart = cart.filter(item => item.cartId !== cartId)
    saveCart(newCart)
  }

  const clearCart = () => {
    saveCart([])
  }

  const filteredProducts = selectedCategory === 'all'
    ? products
    : products.filter(p => p.category === selectedCategory)

  const cartTotal = cart.reduce((sum, item) => sum + item.totalPrice, 0)
  const cartCount = cart.reduce((sum, item) => sum + item.quantity, 0)

  return (
    <div className="min-h-screen bg-gray-50">
      <Navbar 
        session={session} 
        cartCount={cartCount}
        onCartClick={() => setIsCartOpen(true)}
      />

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        
        {/* Category Filter */}
        {categories.length > 1 && (
          <div className="flex flex-wrap gap-3 justify-center mb-12">
            <button
              onClick={() => setSelectedCategory('all')}
              className={`px-6 py-2 rounded-full font-medium transition ${
                selectedCategory === 'all'
                  ? 'bg-black text-white'
                  : 'bg-white border border-gray-300 hover:border-black'
              }`}
            >
              Alle ({products.length})
            </button>
            {categories.map(cat => {
              const count = products.filter(p => p.category === cat).length
              return (
                <button
                  key={cat}
                  onClick={() => setSelectedCategory(cat)}
                  className={`px-6 py-2 rounded-full font-medium transition ${
                    selectedCategory === cat
                      ? 'bg-black text-white'
                      : 'bg-white border border-gray-300 hover:border-black'
                  }`}
                >
                  {cat} ({count})
                </button>
              )
            })}
          </div>
        )}

        {/* Products Grid */}
        {loading ? (
          <div className="text-center py-12">L√§dt...</div>
        ) : (
          <ProductGrid>
            {filteredProducts.map(product => (
              <ProductCard
                key={product.id}
                product={product}
                extras={extras}
                onAddToCart={handleAddToCart}
              />
            ))}
          </ProductGrid>
        )}

        {filteredProducts.length === 0 && !loading && (
          <div className="text-center py-12">
            <p className="text-gray-500">Keine Produkte in dieser Kategorie</p>
          </div>
        )}
      </main>

      {/* MiniCart */}
      <MiniCart
        isOpen={isCartOpen}
        onClose={() => setIsCartOpen(false)}
        cart={cart}
        onUpdateQuantity={updateCartQuantity}
        onRemoveItem={removeFromCart}
        onClearCart={clearCart}
        total={cartTotal}
      />

      {/* Footer */}
      <Footer />
    </div>
  )
}